<template><section><html><head></head><body><h1>ModelGraphics</h1>
<p><code v-pre="">vc-graphics-model</code> 组件用于加载基于 glTF 的 3D 模型。需要作为 <code v-pre="">vc-entity</code> 的子组件使用。</p>
<h2>示例</h2>
<h3>加载模型</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity :position="position1" :description="description" :model.sync="model1" :label.sync="label1">
          <vc-graphics-label ref="label" text="Hello Vue Cesium" font="20px sans-serif"></vc-graphics-label>
          <vc-graphics-model ref="model" :uri="uri1"></vc-graphics-model>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position1"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:model.sync</span>=<span class="hljs-string">"model1"</span> <span class="hljs-attr">:label.sync</span>=<span class="hljs-string">"label1"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-label</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"label"</span> <span class="hljs-attr">text</span>=<span class="hljs-string">"Hello Vue Cesium"</span> <span class="hljs-attr">font</span>=<span class="hljs-string">"20px sans-serif"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-label</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-model</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"model"</span> <span class="hljs-attr">:uri</span>=<span class="hljs-string">"uri1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-model</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">description</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,
        <span class="hljs-attr">model1</span>: {},
        <span class="hljs-attr">label1</span>: {},
        <span class="hljs-attr">position1</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">114.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">1.0</span> },
        <span class="hljs-attr">uri1</span>: <span class="hljs-string">'./statics/SampleData/models/GroundVehicle/GroundVehicle.glb'</span>
      }
    },
    <span class="hljs-title function_">mounted</span>(<span class="hljs-params"></span>) {
      <span class="hljs-title class_">Promise</span>.<span class="hljs-title function_">all</span>([<span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">label</span>.<span class="hljs-property">createPromise</span>, <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">model</span>.<span class="hljs-property">createPromise</span>]).<span class="hljs-title function_">then</span>(<span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
        instances[<span class="hljs-number">0</span>].<span class="hljs-property">viewer</span>.<span class="hljs-title function_">zoomTo</span>(instances[<span class="hljs-number">0</span>].<span class="hljs-property">viewer</span>.<span class="hljs-property">entities</span>)
      })
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = cesiumInstance
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定 model 是否显示。</td>
</tr>
<tr>
<td>uri</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> 指定 model 的 url 地址。</td>
</tr>
<tr>
<td>scale</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> 指定 model 缩放比例。</td>
</tr>
<tr>
<td>minimumPixelSize</td>
<td>Number</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code> 指定 model 的最小像素。</td>
</tr>
<tr>
<td>maximumScale</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 model 最大像素。</td>
</tr>
<tr>
<td>incrementallyLoadTextures</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定在加载模型后纹理是否可以继续流入。</td>
</tr>
<tr>
<td>runAnimations</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定是否启动模型中的动画。</td>
</tr>
<tr>
<td>clampAnimations</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定动画在没有帧动画的时候保持最后一个姿势。</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre="">1</code></td>
<td><code v-pre="">optional</code> 指定 model 是否投射或接收每个光源的阴影。 <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 model 的高度模式。 <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>silhouetteColor</td>
<td>Object|String|Array</td>
<td><code v-pre="">'RED'</code></td>
<td><code v-pre="">optional</code> 指定 model 轮廓线颜色。</td>
</tr>
<tr>
<td>silhouetteSize</td>
<td>Number</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code> 指定 model 轮廓线像素尺寸。</td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre="">'WHITE'</code></td>
<td><code v-pre="">optional</code> 指定 model 渲染混合的颜色。</td>
</tr>
<tr>
<td>colorBlendMode</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 model 与颜色混合模式。 <strong>HIGHLIGHT: 0, REPLACE: 1, MIX: 2</strong></td>
</tr>
<tr>
<td>colorBlendAmount</td>
<td>Number</td>
<td><code v-pre="">0.5</code></td>
<td><code v-pre="">optional</code> 指定 colorBlendMode 为 MIX 的颜色强度。0 表示模型颜色，1 表示纯色，0-1 表示混合。</td>
</tr>
<tr>
<td>imageBasedLightingFactor</td>
<td>Object</td>
<td><code v-pre="">{x: 1.0, y: 1.0}</code></td>
<td><code v-pre="">optional</code> 指定漫反射和镜面反射因子。</td>
</tr>
<tr>
<td>lightColor</td>
<td>Object|String|Array</td>
<td></td>
<td><code v-pre="">optional</code> 指定着色模型时要使用的灯光颜色的属性。未指定是太阳颜色。</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定模型随相机改变的显示条件。 <strong>结构：{ near: number, far: number }</strong></td>
</tr>
<tr>
<td>nodeTransformations</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 设置 TranslationRotationScale 节点转换参数。<strong>结构：{translation: {x: number, y: number, z: number}, rotation: {x: number, y: number, z: number, w: number}, scale:{x: number, y: number, z: number}}</strong></td>
</tr>
<tr>
<td>articulations</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code></td>
</tr>
<tr>
<td>clippingPlanes</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定模型屏幕裁剪参数。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/ModelGraphics.html">ModelGraphics</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>每当更改或修改属性或子属性时触发该事件。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          description: 'Hello Vue Cesium',
          model1: {},
          label1: {},
          position1: { lng: 114.0, lat: 40.0, height: 1.0 },
          uri1: './statics/SampleData/models/GroundVehicle/GroundVehicle.glb'
        }
      },
      mounted() {
        Promise.all([this.$refs.label.createPromise, this.$refs.model.createPromise]).then(
          (instances) => {
            instances[0].viewer.zoomTo(instances[0].viewer.entities)
          }
        )
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
        }
      }
    }
  </script>